<template>
  <div>
    <el-autocomplete
      v-model="shop"
      :fetch-suggestions="querySearchAsync"
      placeholder="商家"
      placement="bottom"
      @select="handleSelect"
    ></el-autocomplete>
    <el-date-picker
      editable="true"
      v-model="date"
      align="up"
      type="date"
      placeholder="選擇日期"
      :picker-options="pickerDateOptions"
    >
    </el-date-picker>
    <el-time-picker
      arrow-control
      editable="true"
      v-model="time"
      align="right"
      format="HH:mm"
      placeholder="選擇時間"
      :picker-options="pickerTimeOptions"
    >
    </el-time-picker>
    <el-input
      placeholder="AB12345678"
      v-model="invoiceNo"
      maxlength="10"
      max="10"
      validate-event="true"
      show-word-limit
      clearable
    >
      <template slot="prepend">發票號碼</template></el-input
    >
    <el-input
      placeholder="0000"
      v-model="randomNo"
      maxlength="4"
      show-word-limit
      clearable
    >
      <template slot="prepend">隨機碼</template></el-input
    >
    <div style="margin: 20px 0"></div>
    <el-input
      type="textarea"
      :autosize="{ minRows: 2, maxRows: 4 }"
      placeholder="備註"
      v-model="text"
    >
    </el-input>
  </div>
</template>

<script>
//這裡可以導入其他文件，比如：組件、工具js、第三方插件js、json文件、圖片文件等等
//例如：import <組件名稱> from '<組件路徑>';

export default {
  //import引入組件需要注入到對象才可以使用
  components: {},
  props: {},
  data() {
    //這裡存放數據
    return {
      restaurants: [],
      shop: "",
      timeout: null,
      pickerDateOptions: {
        firstDayOfWeek: 1,
        shortcuts: [
          {
            text: "今天",
            onClick(picker) {
              picker.$emit("pick", new Date());
            },
          },
        ],
      },
      pickerTimeOptions: {
        shortcuts: [
          {
            text: "現在",
            onClick(picker) {
              picker.$emit("pick", new Date());
            },
          },
        ],
        format: "HH:mm",
      },
      date: "",
      time: "",
      invoiceNo: "",
      randomNo: "",
      text: "",
    };
  },

  //計算屬性 類似於data概念
  computed: {},
  //監看data數據中的變化
  watch: {},
  //方法集合
  methods: {
    loadAll() {
      return [
        { value: "三全鲜食（北新泾店）", address: "长宁区新渔路144号" },
        {
          value: "Hot honey 首尔炸鸡（仙霞路）",
          address: "上海市长宁区淞虹路661号",
        },
        {
          value: "新旺角茶餐厅",
          address: "上海市普陀区真北路988号创邑金沙谷6号楼113",
        },
        { value: "泷千家(天山西路店)", address: "天山西路438号" },
        {
          value: "胖仙女纸杯蛋糕（上海凌空店）",
          address: "上海市长宁区金钟路968号1幢18号楼一层商铺18-101",
        },
        { value: "贡茶", address: "上海市长宁区金钟路633号" },
        {
          value: "豪大大香鸡排超级奶爸",
          address: "上海市嘉定区曹安公路曹安路1685号",
        },
        {
          value: "茶芝兰（奶茶，手抓饼）",
          address: "上海市普陀区同普路1435号",
        },
        { value: "十二泷町", address: "上海市北翟路1444弄81号B幢-107" },
        { value: "星移浓缩咖啡", address: "上海市嘉定区新郁路817号" },
        { value: "阿姨奶茶/豪大大", address: "嘉定区曹安路1611号" },
        { value: "新麦甜四季甜品炸鸡", address: "嘉定区曹安公路2383弄55号" },
        {
          value: "Monica摩托主题咖啡店",
          address: "嘉定区江桥镇曹安公路2409号1F，2383弄62号1F",
        },
        {
          value: "浮生若茶（凌空soho店）",
          address: "上海长宁区金钟路968号9号楼地下一层",
        },
        { value: "NONO JUICE  鲜榨果汁", address: "上海市长宁区天山西路119号" },
        { value: "CoCo都可(北新泾店）", address: "上海市长宁区仙霞西路" },
        {
          value: "快乐柠檬（神州智慧店）",
          address: "上海市长宁区天山西路567号1层R117号店铺",
        },
        {
          value: "Merci Paul cafe",
          address: "上海市普陀区光复西路丹巴路28弄6号楼819",
        },
        {
          value: "猫山王（西郊百联店）",
          address: "上海市长宁区仙霞西路88号第一层G05-F01-1-306",
        },
        { value: "枪会山", address: "上海市普陀区棕榈路" },
        { value: "纵食", address: "元丰天山花园(东门) 双流路267号" },
        { value: "钱记", address: "上海市长宁区天山西路" },
        { value: "壹杯加", address: "上海市长宁区通协路" },
        {
          value: "唦哇嘀咖",
          address: "上海市长宁区新泾镇金钟路999号2幢（B幢）第01层第1-02A单元",
        },
        { value: "爱茜茜里(西郊百联)", address: "长宁区仙霞西路88号1305室" },
        {
          value: "爱茜茜里(近铁广场)",
          address:
            "上海市普陀区真北路818号近铁城市广场北区地下二楼N-B2-O2-C商铺",
        },
      ];
    },
    querySearchAsync(queryString, cb) {
      var restaurants = this.restaurants;
      var results = queryString
        ? restaurants.filter(this.createStateFilter(queryString))
        : restaurants;

      clearTimeout(this.timeout);
      this.timeout = setTimeout(() => {
        cb(results);
      }, 3000 * Math.random());
    },
    createStateFilter(queryString) {
      return (state) => {
        return (
          state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0
        );
      };
    },
    handleSelect(item) {
      console.log(item);
    },
  },

  //生命週期 - 創建完成（可以訪問當前this實例）
  created() {},
  //生命週期 - 掛載完成（可以訪問DOM元素）
  mounted() {
    this.restaurants = this.loadAll();
  },
  beforeCreate() {}, //生命週期 - 創建之前
  beforeMount() {}, //生命週期 - 掛載之前
  beforeUpdate() {}, //生命週期 - 更新之前
  updated() {}, //生命週期 - 更新之後
  beforeDestory() {}, //生命週期 - 銷毀之前
  actived() {}, //如果頁面有keep-alive緩存功能，這個函數會觸發
};
</script>

<style scoped>
.el-select .el-input {
  width: 130px;
}
.input-with-select .el-input-group__prepend {
  background-color: #fff;
}
</style>


